.news-list > ul > li:hover,
.news-list > ul > li.active {
  background: rgba(255, 255, 255, 0.4);
  -webkit-box-shadow: 0 0 10px 0 rgba(0, 27, 54, 0.3);
  -moz-box-shadow: 0 0 10px 0 rgba(0, 27, 54, 0.3);
  -o-kit-box-shadow: 0 0 10px 0 rgba(0, 27, 54, 0.3);
  -ms-box-shadow: 0 0 10px 0 rgba(0, 27, 54, 0.3);
