}
.news-list > ul > li:last-child .news-right .new {
  bottom: 86px;
}
.news-list > ul > li:last-child .news-right .new:before {
  top: auto;
  bottom: -12px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 12px solid #d3dbe3;
  border-bottom: none;
}
.news-list > ul > li:last-child .news-right .new:after {
  top: auto;
  bottom: -11px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 12px solid #fff;
  border-bottom: none;
}
