  width: 280px;
  border-radius: 3px;
}
.news-right .new:before {
  content: '';
  z-index: 8;
  position: absolute;
  left: 10px;
  top: -14px;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 12px solid #d3dbe3;
}
.news-right .new:after {
  content: '';
  z-index: 11;
  position: absolute;
  left: 10px;
  top: -12px;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 12px solid #fff;
}
.news-right .new a {
  position: relative;
  height: 20px;
  line-height: 20px;
  padding-left: 10px;
  display: block;
  font-weight: normal;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.news-right .new a:after {
  content: '';
  position: absolute;
  left: 0;
  top: 3px;
  width: 2px;
  height: 14px;
  border-radius: 2px;
  background: #06bdf2;
}
.news-right .news-new {
  position: absolute;
